<template>
  <div class="container">
    <img class="logo-img" src="../../assets/xiaoe_logo.png"/>
    <Form class="form-container" ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80">
      <FormItem label="Account" prop="account">
        <Input type="text" v-model="formCustom.account" placeholder="Username">
          <Icon type="ios-person-outline" slot="prepend"></Icon>
          <!-- eslint-disable-next-line -->
        </Input>
      </FormItem>
      <FormItem label="Password" prop="passwd">
        <Input type="password" v-model="formCustom.passwd"  placeholder="Password"> 
          <Icon type="ios-lock-outline" slot="prepend"></Icon>
        <!-- eslint-disable-next-line -->
        </Input>
      </FormItem>
      <FormItem class="bottom-item">
          <Button type="primary" @click="siginInClick('formCustom')">登陆</Button>
        </FormItem>
        <FormItem class="bottom-item">
          <Button type="primary" @click="signUpClick('formCustom')">注册</Button>
        </FormItem>
    </Form>
  </div>
</template>

<script>
import axios from 'axios'
// eslint-disable-next-line
import {setCookie, getCookie, delCookie} from '../../utils/cookie.js'
export default {
  data() {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please enter your password'));
      } else {
        callback();
      }
    };
    const validateAccount = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('account cannot be empty'));
      }
      callback()
      // 模拟异步验证效果
      // setTimeout(() => {
        // if (!Number.isInteger(value)) {
        //   callback(new Error('Please enter a numeric value'));
        // } else {
        //   if (value < 18) {
        //     callback(new Error('Must be over 18 years of age'));
        //   } else {
        //     callback();
        //   }
        // }
      // }, 1000);
    };
    return {
      formCustom: {
        account: '',
        passwd: '',
      },
      ruleCustom: {
        passwd: [
          { 
            validator: validatePass, 
            trigger: 'blur' 
          }
        ],
        account: [
          { 
            validator: validateAccount, 
            trigger: 'blur' 
          }
        ]
      }
    }
  },
  methods: {
    siginInClick (item) {
      this.$refs[item].validate((valid) => {
        if (valid) {
          
          axios({
            method: 'post',
            url: 'http://127.0.0.1:5000/v1/api/admins/sign_in',
            data: {
              "admin": {
                "name": this.formCustom.account,
                "password": this.formCustom.passwd
              }
            },
            headers: {'Content-Type': 'text/plain'}
          }).then((res) => {
            if (res["data"].code == 0) {
              this.$Message.success('登录成功');
              setCookie('app_version_manager', res["data"].data.auth_token)
              this.$router.push({name:"homepage"})
            } else if (res["data"].code == 3000) {
              this.$Message.error('登录失败');
            } else if (res["data"].code == 4000) {
              this.$Message.error('登录失败');
            } else {
              this.$Message.error('登录失败');
            }
          }).catch((err)=>{
            // eslint-disable-next-line
            console.log("登录失败1", err)
            this.$Message.error('登录失败');
          })
        } else {
          this.$Message.error('帐号密码格式不正确');
        }
      })
      
    },
    signUpClick(item) {
      this.$refs[item].validate((valid) => {
        if (valid) {
          axios({
            method: 'post',
            url: 'http://127.0.0.1:5000/v1/api/admins/sign_up',
            data: {
              "admin": {
                "name": this.formCustom.account,
                "password": this.formCustom.passwd
              }
            },
            headers: {'Content-Type': 'text/plain'}
          }).then((res) => {
            // eslint-disable-next-line
            console.log("注册成功", res)
            if (res["data"].code == 0) {
              this.$Message.success('注册成功');
              setCookie('app_version_manager', res["data"].data.auth_token)
              this.$router.push({name:"homepage"})
            } else {
              this.$Message.error('注册失败');
            }
          }).catch((err)=>{
            // eslint-disable-next-line
            console.log("注册失败", err)
            this.$Message.error('登录失败');
          })
        } else {
          this.$Message.error('帐号密码格式不正确');
        }
      })
      
    }
  }
}
</script>


<style scoped>
.container {
  min-height: 700px;
  background: #f5f7f9;
  display: flex;
  justify-content: center;
}

.form-container {
  width: 500px;
  height: 300px;
  margin-top: 250px;
}

.logo-img {
  position: absolute;
  width: 231px;
  height: 70px;
  left: 50%;
  top: 150px;
  transform: translateX(-50%);
}

.bottom-item {
}
</style>
