<template>
  <div></div>
</template>

<script>
// eslint-disable-next-line
import {setCookie, getCookie, delCookie} from '../../utils/cookie.js'
import axios from 'axios'
export default {
  data () {
    return {

    }
  },
  beforeRouteEnter (to, from, next) {    

    // 每次请求携带cookies信息
    // axios.defaults.withCredentials = true
    // 创建axios实例
    const service = axios.create({
    //   // baseURL: 'http://127.0.0.1:5000/v1/api', // api的base_url
      timeout: 15000 // 请求超时时间
    })
    // // request拦截器
    service.interceptors.request.use(config => {
      // console.log(store.getters.token)
      // if (store.getters.token) {
        // console.log(getToken())
        // config.headers['X-Token'] = getToken() // 让每个请求携带自定义token 请根据实际情况自行修改
        // var token = getToken()
        Object.assign(config.headers, {
        'Access-Control-Allow-Origin': '*' })
      // }
      return config
    }, error => {
      // Do something with request error
      // console.log(error) // for debug
      Promise.reject(error)
    })

    axios('http://127.0.0.1:5000/v1/api/test_token', {
      method: 'get',
      headers: {
        'Authorization': 'Bearer ' + getCookie("app_version_manager"),
      }
    }).then((res) => {
      // eslint-disable-next-line
      console.log("token ok", res, getCookie("app_version_manager"))
      if (res["data"].code == 0) {
        next()
      } else {
        next({
          path: "/login",
        })
      }
    }).catch((err)=>{
      // eslint-disable-next-line
      console.log("token fail", err, getCookie("app_version_manager"))
      next({
        path: "/login",
      })
    })
  }
}
</script>
